import Sidebar from "@components/sidebar";
import { yupResolver } from "@hookform/resolvers/yup";
import classnames from "classnames";
import { convertToRaw, EditorState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { useEffect, useState } from "react";
import { Editor } from "react-draft-wysiwyg";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label, Media, Row } from "reactstrap";
import validateOptions from "../../../../constants/validate";
import { convertFileToBase64, uploadImage } from "../../../../helper/common";
import { BG_BLANK } from "./../../../../constants/app";
import { add } from "../store/action";
import * as yup from "yup";
import { toast } from "react-toastify";
import classNames from "classnames";
import avatarBlank from "../../../../assets/images/avatars/bg-blank.png";
import "./style.scss";

const SidebarAdd = ({
  open,
  toggleSidebar,
  avatar,
  setAvatar,
  intl,
  disable,
  setDisable,
}) => {
  const [productData, setProductData] = useState(null);
  const [fileImage, setFileImage] = useState();
  const ProductOptions = validateOptions.ProductOptions;
  const store = useSelector((state) => state.products);
  const { instractors } = useSelector((state) => state.products);
  const { data } = useSelector((state) => state.category);
  const { categoryProduct } = useSelector((state) => state.categoryProduct);
  const [changeSelect, setChangeSelect] = useState();
  const [changeSelectProduct, setChangeSelectProduct] = useState();
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const cate = yup.object({
    // categoryId: yup
    //   .number()
    //   .required(<FormattedMessage id="The category field is required" />),
    product_name: yup
      .string()
      .required(<FormattedMessage id="The product's name field is required" />)
      .min(
        3,
        <FormattedMessage id="Product name must be at least 3 characters" />
      ),
    number_trial: yup
      .number()
      .typeError(<FormattedMessage id="The number day field must be numeric" />)
      .min(
        0,
        <FormattedMessage id="Number day must be greater than or equal to 0" />
      )
      .max(
        30,
        <FormattedMessage id="Number day must be less than or equal to 30" />
      ),
  });
  const [isCourse, setIscourse] = useState(false);
  const [isService, setIsService] = useState(false);
  const [isProduct, setIsProduct] = useState(false);
  const [Course, setCourse] = useState();
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    getValues,
    setValue,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
    defaultValues: {
      number_trial: 0, // Giá trị mặc định của number_trial là 0
    },
  });
  const onChange = async (e) => {
    const imageBase64 = await convertFileToBase64(e.target.files[0]);
    setFileImage(e.target.files[0]);
    setAvatar(imageBase64);
  };
  useEffect(() => {
    if (Course == 0) {
      setIscourse(true);
    } else {
      setIscourse(false);
    }
    if (Course == 3) {
      setIsService(true);
    } else {
      setIsService(false);
    }
    if (Course == 2) {
      setIsProduct(true);
    } else {
      setIsProduct(false);
    }
  }, [Course]);
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  const [valueDescription, setValueDescription] = useState(
    EditorState.createEmpty()
  );

  const [valueSortDescription, setValueSortDescription] = useState(
    EditorState.createEmpty()
  );
  const onSubmit = async (values) => {
    const product_description =
      (valueDescription &&
        draftToHtml(convertToRaw(valueDescription?.getCurrentContent()))) ||
      null;

    const product_sort_description =
      (valueSortDescription &&
        draftToHtml(convertToRaw(valueSortDescription?.getCurrentContent()))) ||
      null;

    setDisable(true);
    var urlImage = " ";

    if (avatar) {
      const newData = avatar.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        if (newData[1] == BG_BLANK) {
          toast.error(<FormattedMessage id="Image is required" />);
          setDisable(false);
          return;
        }
        const data = {
          imageData: newData[1],
          imageFormat: fileImage?.type?.split("/")[1] || "png",
        };

        await uploadImage(data, fileImage).then((res) => {
          urlImage = res?.data;
        });
      } else {
        urlImage = avatar;
      }
      if (urlImage) {
        dispatch(
          add({
            product_name: values?.product_name || "",
            sort_product_description: product_sort_description || "",
            url_ytb: values.url_ytb || " ",
            url_demo: values.url_demo || " ",
            url_tutorial: values.url_tutorial || " ",
            product_description: product_description || " ",
            instructorId: values?.instructorId || null,
            lang,
            status: parseInt(values.status),
            image: urlImage,
            categoryProductId: changeSelect || null,
            cateProId: changeSelectProduct || null,
            number_trial: values?.number_trial || 0,
            link_trial: "/",
            link_download: "/", //cancel
            open: 1,
            is_monitor: 0,
            vat: Number(values?.vat) || 0,
            allow_display: parseInt(values.allow_display),
            product_type: parseInt(values.product_type),
          })
        );
      }
    }
  };
  const uploadCallback = async (file) => {
    var linkImg = avatarBlank;
    const imageBase64 = await convertFileToBase64(file);
    if (imageBase64) {
      const newData = imageBase64.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        const data = {
          imageData: newData[1],
          imageFormat: file?.type?.split("/")[1] || "png",
        };
        await uploadImage(data, file).then((res) => {
          linkImg = res?.data;
        });
      }
    }
    return new Promise((resolve, reject) => {
      resolve({ data: { link: linkImg } });
    });
  };

  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="New Product" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label for="product_name">
            <FormattedMessage id="product name" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="product_name"
            id="product_name"
            innerRef={register(ProductOptions.product_name)}
            className={classNames({ "is-invalid": errors["product_name"] })}
            onBlur={() => {
              let productName1 = document.getElementById("product_name");
              if (productName1 && productName1.value) {
                productName1.value = productName1.value.trim();
              }
            }}
          />
          <small className="text-danger">
            {errors?.product_name && errors.product_name.message}
          </small>
          {errors?.product_name?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid product's name" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="Number of trial days" />
          </Label>
          <Input
            name="number_trial"
            id="number_trial"
            type="number"
            innerRef={register(cate.number_trial)}
            onBlur={() => {
              let number_trial = document.getElementById("number_trial");
              if (number_trial && number_trial.value) {
                number_trial.value = number_trial.value.trim();
              }
            }}
            className={classNames({ "is-invalid": errors["number_trial"] })}
          />
          <small className="text-danger">
            {errors?.number_trial && errors.number_trial.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="vat">
            <FormattedMessage id="Vat" />
          </Label>
          <Input
            name="vat"
            id="vat"
            type="number"
            innerRef={register(ProductOptions.vat)}
            onBlur={() => {
              let vat = document.getElementById("vat");
              if (vat && vat.value) {
                vat.value = vat.value.trim();
              }
            }}
            className={classNames({ "is-invalid": errors["vat"] })}
          />
          <small className="text-danger">
            {errors?.vat && errors.vat.message}
          </small>
        </FormGroup>
        {isCourse && (
          <FormGroup>
            <Label for="url_ytb">
              <FormattedMessage id="Link Youtube" />{" "}
            </Label>
            <Input
              name="url_ytb"
              id="url_ytb"
              placeholder=" "
              innerRef={register(ProductOptions.url_ytb)}
            />
          </FormGroup>
        )}

        {isService && (
          <>
            <FormGroup>
              <Label for="url_demo">
                <FormattedMessage id="Link Demo" />{" "}
              </Label>
              <Input
                name="url_demo"
                id="url_demo"
                placeholder=" "
                innerRef={register(ProductOptions.url_demo)}
              />
            </FormGroup>
            <FormGroup>
              <Label for="url_tutorial">
                <FormattedMessage id="Link Tutorial" />{" "}
              </Label>
              <Input
                name="url_tutorial"
                id="url_tutorial"
                placeholder=" "
                innerRef={register(ProductOptions.url_tutorial)}
              />
            </FormGroup>
          </>
        )}

        <FormGroup>
          <Label for="sort_product_description">
            <FormattedMessage id="Short Content" />{" "}
          </Label>

          <Editor
            stripPastedStyles={true}
            toolbar={{
              options: ["inline", "textAlign"],
              inline: {
                inDropdown: false,
                options: ["bold", "italic", "underline"],
              },
            }}
            editorState={valueSortDescription}
            onEditorStateChange={(data) => setValueSortDescription(data)}
            name="sort_product_description"
            innerRef={register(ProductOptions.sort_description_en)}
          />
          <small className="text-danger">
            {errors?.description && errors.description.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="product_description">
            <FormattedMessage id="Description" />{" "}
            <span className="text-danger"></span>
          </Label>

          <Editor
            stripPastedStyles={true}
            toolbar={{
              // options: ["inline", "textAlign"],
              // inline: {
              //   inDropdown: false,
              //   options: ["bold", "italic", "underline"],
              // },
              image: { uploadCallback: uploadCallback },
            }}
            editorState={valueDescription}
            onEditorStateChange={(data) => setValueDescription(data)}
            name="product_description"
            innerRef={register(ProductOptions.description_en)}
            // className={classNames({
            //   "is-invalid": errors["product_description"],
            // })}
          />
          <small className="text-danger">
            {errors?.description_en && errors.description_en.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="status">
            <FormattedMessage id="status" />
          </Label>

          <Input
            type="select"
            name="status"
            id="status"
            defaultValue={productData && productData.status}
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Active" })}</option>
            <option value="2">
              {intl.formatMessage({ id: "OutOfStock" })}
            </option>
          </Input>
        </FormGroup>

        <FormGroup>
          <Label for="allow_display">
            <FormattedMessage id="Allow" />
          </Label>

          <Input
            type="select"
            name="allow_display"
            id="allow_display"
            defaultValue={productData && productData.allow_display}
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Allow" })}</option>
            <option value="0">
              {intl.formatMessage({ id: "Do not allow" })}
            </option>
          </Input>
        </FormGroup>

        <FormGroup>
          <Label for="product_type">
            <FormattedMessage id="product_type" />
          </Label>

          <Input
            type="select"
            name="product_type"
            id="product_type"
            defaultValue={productData && productData.product_type}
            innerRef={register({ required: true })}
            onChange={(e) => {
              setCourse(e.target.value);
            }}
          >
            <option value="1">{intl.formatMessage({ id: "Book" })}</option>
            <option value="3">{intl.formatMessage({ id: "service" })}</option>
            <option value="2">{intl.formatMessage({ id: "Product" })}</option>
            <option value="0">{intl.formatMessage({ id: "Course" })}</option>
          </Input>
        </FormGroup>

        {!isProduct && (
          <FormGroup>
            <Label>
              <FormattedMessage id="categoryId" />
              <span className="text-danger"> * </span>
            </Label>
            <Controller
              control={control}
              name="categoryId"
              render={({ field }) => {
                return (
                  <Select
                    id="categoryId"
                    innerRef={register}
                    name="categoryId"
                    className={classnames(
                      "react-select",
                      !changeSelect
                        ? {
                            "is-invalid": errors["categoryId"],
                          }
                        : ""
                    )}
                    options={data?.map((item, index) => {
                      return {
                        value: item?.id,
                        label: item?.name,
                        number: index + 1,
                      };
                    })}
                    classNamePrefix="select"
                    {...field}
                    onChange={(e) => {
                      setError("categoryId", "");
                      setValue("categoryId", e?.value);
                      setChangeSelect(e?.value);
                    }}
                  />
                );
              }}
            ></Controller>
            <small className="text-danger">
              {errors?.categoryId && errors.categoryId.message}
            </small>
          </FormGroup>
        )}

        {isProduct && (
          <FormGroup>
            <Label>
              <FormattedMessage id="section content product" />
              <span className="text-danger"> * </span>
            </Label>
            <Controller
              control={control}
              name="cateProId"
              render={({ field }) => {
                return (
                  <Select
                    id="cateProId"
                    innerRef={register}
                    name="cateProId"
                    className={classnames(
                      "react-select",
                      !changeSelectProduct
                        ? {
                            "is-invalid": errors["cateProId"],
                          }
                        : ""
                    )}
                    options={categoryProduct?.map((item, index) => {
                      return {
                        value: item?.id,
                        label: item?.name,
                        number: index + 1,
                      };
                    })}
                    classNamePrefix="select"
                    {...field}
                    onChange={(e) => {
                      setError("cateProId", "");
                      setValue("cateProId", e?.value);
                      setChangeSelectProduct(e?.value);
                    }}
                  />
                );
              }}
            ></Controller>
            <small className="text-danger">
              {errors?.cateProId && errors.cateProId.message}
            </small>
          </FormGroup>
        )}

        {isCourse && (
          <FormGroup>
            <Label>
              <FormattedMessage id="instructors" />
            </Label>
            <Controller
              control={control}
              name="instructorId"
              render={({ field }) => {
                return (
                  <Select
                    id="instructorId"
                    innerRef={register}
                    name="instructorId"
                    // placeholder={<FormattedMessage id="Select..." />}
                    className={classnames(
                      "react-select",
                      !changeSelect
                        ? {
                            "is-invalid": errors["instructorId"],
                          }
                        : ""
                    )}
                    options={instractors?.map((item, index) => {
                      return {
                        value: item?.id,
                        label: `${item?.last_name} ${item?.first_name}`,
                        number: index + 1,
                      };
                    })}
                    classNamePrefix="select"
                    {...field}
                    onChange={(e) => {
                      setError("instructorId", "");
                      setValue("instructorId", e?.value);
                    }}
                  />
                );
              }}
            ></Controller>
            <small className="text-danger">
              {errors?.instructorId && errors.instructorId.message}
            </small>
          </FormGroup>
        )}

        <FormGroup>
          <Label for="Image">
            <FormattedMessage id="Image" />
            <span className="text-danger"> *</span>
          </Label>
          <Row className="align-items-end p-1">
            <Media className="mr-25" left>
              <Media
                object
                className="rounded mr-50"
                src={avatar}
                height="100"
                width="100"
              />
            </Media>{" "}
            <Row className="flex-column px-1">
              <Button.Ripple tag={Label} className="mt-1" color="primary">
                <FormattedMessage id="Upload" />
                <Input
                  type="file"
                  onChange={(e) => onChange(e)}
                  hidden
                  accept="image/*"
                />
              </Button.Ripple>
            </Row>
          </Row>
        </FormGroup>
        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="add" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarAdd);
